function FilterLink(props){
    let {onClick, type, disabled} = props;
    const label = (type === 'add') ? 'Another' : 'Remove';
    const icon = (type === 'add') ? 'plus' : 'minus';
    if(disabled) onClick = () => {};

    // return
    const css = classNames('lds-filters1-link', disabled && 'is-disabled');
    return <div onClick={onClick} className={css}><i className={`lds-icon is-type-${icon}-circle-primary`} />{label}</div>;
}

function FilterRow(props){
    const {index, onClickAdd, onClickRemove, disabled, id, } = props;
    const [isDisabled, setDisabled] = React.useState(0);
    const [filter, setFilter] = React.useState(0);
    let [operatorOptions, setOperatorOptions] = React.useState(operators.text);
    // const [operatorOptions, setOperatorOptions] = React.useState(false);

    // useEffect(() => {
    //     setDisabled(disabled);
    // }, [disabled])

    const onClickRemove2 = (e) => {
        if(onClickRemove) onClickRemove(id);
    }

    // const options = 
    const onChangeType = (item) => {
        const value = item.value;
        const dataType = types[value];
        setFilter(value);
        setOperatorOptions(operators[dataType]);
        setTextValue('');
        console.log('--- DATA TYPE: ', dataType);
        console.log('--- OPTION: ', item);
    }

    const [textValue, setTextValue] = React.useState(props.value);
    const onTextChange = (e) => {
        setTextValue(e?.target?.value);
    };

    const is_empty = filter ? false : true;
    const is_first = !is_empty && (index === 0);
    const dataType = types[filter];
    const valueOptions2 = valueOptions[dataType];
    const no_value = noValueTypes[dataType] ? true : false; // (dataType === 'comm');

    var show_operator = true;
    var show_input = true;
    var show_select = false;
    var show_remove = true;
    var show_add = true;

    console.log('IS_EMPTY: ' + is_empty + ', FILTER:', filter);

    if(is_empty){
        // no filter
        show_operator = show_input = show_remove = show_add = false;
    }else if(valueOptions2){
        // has value options
        operatorOptions = operators['isisnot'];
        show_operator = true;
        show_input = false;
        show_select = true;
    }else if(no_value){
        // no text value input
        show_operator = true;
        show_input = false;
    }

    if(is_first) show_remove = false;


    // const show_select = !no_value && !is_empty && !show_input;

    console.log('===== no_value222:' + no_value + ', is_empty: ' + is_empty);


    // return
    const row_css = utils.classNames('lds-filters1row', is_empty && 'is-empty', is_first && 'is-first', isDisabled && 'is-disabled', no_value && 'is-novalue');
    const select_css = utils.classNames('lds-filterselect', !filter && 'is-empty');

    // --- TEMP
    return (

            <div className="lds-filters1-group">
                <span className="lds-filters1-and">And</span>
                {/* <span className="lds-filters1-and">And</span> */}

                {/* INPUTS */}
                <Input.Select short disabled={isDisabled} placeholder="Select" label="Select..." value={filter} onChange={onChangeType} options={options} style={{width: 236, }} />
                {show_operator && <Input.Select short disabled={isDisabled} placeholder="Select" label="" options={operatorOptions} style={{width: 214, }} />}
                {show_input && <Input short disabled={isDisabled} placeholder="Select" label="" options={options} style={{width: 225, }} />}
                {show_select && <Input.Select short disabled={isDisabled} placeholder="Select" label="" options={valueOptions2} style={{width: 225, }} />}

                {/* BUTTONS */}
                <div className="lds-filters1-link" onClick={onClickRemove2} ><i className="lds-icon is-type-trash" /></div>
                {/* {id} */}
                {/* <FilterLink disabled={isDisabled} onClick={onClickRemove2} type="trash" /> */}
                {false && <FilterLink disabled={isDisabled} onClick={onClickRemove2} type="remove" />}
                {/* {show_add && <FilterLink disabled={isDisabled} onClick={onClickAdd2} type="add" />} */}

            </div>

    );
}

function ModalItem(props){
    const {id, onRemove} = props;
    const onRemove2 = () => {
        if(onRemove) onRemove(id);
    }
    return (<div onClick={() => onRemove2()}>{`This is ID: ${id}`}</div>);
}

var globalItems = [];
var _uniq = 99;
function uniq(){return _uniq++;}

const MessageModal = (props) => {
    const dispatch = useDispatch();
    const [items, setItems] = React.useState([]);

    useEffect(() => {
        globalItems = [];
        onClickAdd();
    }, []);    

    const onClickCancel = () => {
        dispatch({call: "HIDE_MODAL"});
    }

    function onRemoveItem(id){
        // alert('REMOVE ID: ' + id);
        const index = globalItems.findIndex(obj => obj.id ===id);
        globalItems.splice(index, 1);
        setItems([...globalItems]);
    }

    function onClickAdd(){
        var id = uniq();
        // globalItems.push({id: id, element: <ModalItem id={id} onRemove={onRemoveItem} />, });
        const index = globalItems.length - 1;
        globalItems.push({id: id, element: <FilterRow onClickRemove={onRemoveItem} index={index} id={id} />, });

        setItems([...globalItems]);
    }

    const [status, setStatus] = React.useState('1');
    const [isDisabled, setDisabled] = React.useState(true);
    const onStatusClick = (item) => {
        const value = parseInt(item.value);
        setStatus(item.value);
        setDisabled(value !== 3);
    }

    // const css = utils.;
    const css = utils.classNames('lds-filters1list', isDisabled && `is-disabled`);
    return (
        <Modal noLoader className={css} size="lg" label="Edit Form" buttons={{ok: onClickCancel, cancel: onClickCancel, }}>
        

            <Sprite.Container id="lds-filters1form">

                <Input.Radio debug={0} groupValue={status} onClick={onStatusClick} name="status" value="1" sprite style={{top: 490, width: 190, }} />
                <Input.Radio debug={0} groupValue={status} onClick={onStatusClick} name="status" value="2" sprite style={{top: 538, width: 190, }} />
                <Input.Radio debug={0} groupValue={status} onClick={onStatusClick} name="status" value="3" sprite style={{top: 651, width: 190, }} />

            </Sprite.Container>
            {/* <div>asdf</div> */}

            <div className={css} >

                {items.map((item) => item.element)}
                <div className="lds-filters1list-blocker" />
                
                <FilterLink disabled={isDisabled} onClick={onClickAdd} type="add" />
                {/* <div onClick={onClickAdd}>Add</div> */}
            </div>
            
            <Sprite.Container id="lds-filters1form-bottom">
            </Sprite.Container>

        </Modal>
    )
}


