function makeTimeOptions(breakValue1){
    var timeOptions = [];
    var time = 7;
    var ampm = ' AM';
    for(var i=0; i<14; i++){
        // if(breakValue1 && i < breakValue1) continue; // skip
        if(time > 12) time = 1;
        if(time > 11) ampm = ' PM';
        var str = (time < 10) ? ('0'+time) : (''+time);
        timeOptions.push(`${str}:00${ampm}`,`${str}:15${ampm}`,`${str}:30${ampm}`,`${str}:45${ampm}`);
        time++;
    }
    timeOptions.push(`0${time}:00${ampm}`);
    // const typeOptions = ['07:00 AM', '07:00 AM', 'Transfer']; // --- NEW
   
    if(breakValue1){
        var arr = [];
        for(var i=breakValue1; i<timeOptions.length; i++){
            arr.push(timeOptions[i]);
        }
        timeOptions = arr;
    }

    return timeOptions;
}

function Modal2(props){
    const {onSubmit, initData, } = props;
    const dispatch = useDispatch();
    const [step, setStep] = React.useState(0);
    const [isError, setError] = React.useState(false);
    const [breakTime1, setBreakTime1] = React.useState(undefined);
    const [breakTime2, setBreakTime2] = React.useState(undefined);
    const [breakOptions2, setBreakOptions2] = React.useState([]);
    const timeOptions = makeTimeOptions();

    
    // on submit
    const [startTime, setStartTime] = React.useState(undefined); // EX: "12:00 PM"
    const [endTime, setEndTime] = React.useState(undefined); // EX: "01:00 PM"

    React.useEffect(() => {

        // setTimeout(() => {
        // }, 200);

        const hasBreak = initData?.hasBreak;
        if(!hasBreak) return; // no init data

        // has init data...
        console.log('========= INIT DATA: ', initData);
        // breakTime1('');
        // const startIndex = initData.startIndex;
        setStep(1);
        setBreakTime1(initData.startIndex);
        onBreakChange1({
            label: initData.startTime,
            value: initData.startIndex,
        }, true);

        onBreakChange2({
            label: initData.endTime,
            value: initData.endIndex,
        });


        // // const timeOptions = makeTimeOptions();
        // const item = timeOptions[initData.startIndex - 1];
        // console.log('========= ITEM: ', item);


        // setTimeout(() => {
        //     onBreakChange1({
        //         label: initData.startTime,
        //         value: initData.startIndex,
        //     }, true);
        // });



    }, [initData]);


    const onClickBreak = () => {
        setStep(1);
    }

    const onClickRemove = () => {
        setStep(0);
    }

    const onClickCancel = () => {
        // alert('onClickCancel');
        onSubmit({});
        dispatch({call: "HIDE_MODAL"});
    }
    
    const onClickOK = () => {
        const hasBreak = (step !== 0 && !!breakTime1);
        if(onSubmit){
            // alert('ON SUBMIT: ' + breakTime1);
            onSubmit({
                hasBreak: hasBreak, 
                // startItem: startItem,
                startTime: startTime, 
                startIndex: breakTime1,
                endTime: endTime, 
                endIndex: breakTime2,
            });
        } 
        dispatch({call: "HIDE_MODAL"});
    }

    const onBreakChange1 = (item, fromInit) => {
        console.log('=========== ON BREAK CHANGE 1: ', item);
        const index = item.value;
        const startTime = item.label;
        const endTime = timeOptions[item.value + 3];

        // console.log('=========== START TIME: ', item);
        // console.log('=========== END TIME: ', endTime);

        // update
        setBreakTime1(item.value); // break starts
        setBreakTime2(4); // break ends 1 hour after break starts by default (4 * 15 = 60)
        const timeOptions2 = makeTimeOptions(item.value); // only show time options happening after break starts
        setBreakOptions2(timeOptions2);
        
        // set form data
        setStartTime(startTime);
        setEndTime(endTime);
        // alert('startTime: ' + startTime + ', endTime: ' + endTime);

    }

    const onBreakChange2 = (item) => {
        // console.log('=========== VALUE: ', item);
        setBreakTime2(item.value);
        // alert('---- END: ' + item.label);
        setEndTime(item.label);
    }

    const css = utils.classNames(step && 'is-step-1');
    const showAdd = (step === 0);
    const showBreak = (step !== 0);
    const showBreakTime2 = (showBreak && breakTime1);


    return (
        <Modal noLoader size="lg" label="Edit Form" buttons={{ok: onClickOK, cancel: onClickCancel, }}>

            <Sprite.Container id="lds-hours1form" className={css}>

                <div className="is-abs-all">
                    
                {showAdd && <Sprite.Button debug={1} onClick={onClickBreak} style={{width: 114, top: 234, left: 560, height: 34, borderRadius:6  }}  />}

                {/* has break */}
                {showBreak && <Sprite.Button debug={0} onClick={onClickRemove} style={{width: 130, top: 309, left: 562, height: 34, borderRadius:6  }}  />}
                {showBreak && <Sprite.Button debug={0} style={{width: 154, top: 289, left: 201, height: 71, borderRadius: 6 }}  />}
                {showBreak && <Input.Select sprite debug={0} value={breakTime1} className="is-empty" onChange={onBreakChange1} label="Start break" options={timeOptions} style={{width: 154, top: 296, left: 208, }} />}
                
                {/* has break and start */}
                {showBreakTime2 && <Input.Select sprite debug={0} value={breakTime2} onChange={onBreakChange2} label="End break" options={breakOptions2} style={{width: 154, top: 296, left: 400, }} />}
                
                

                </div>

            </Sprite.Container> 


            
        </Modal>
    );
}


function removeZero(str){
    if(typeof(str) !== 'string') return;
    const parts = str.split('0');
    if(parts[0] === '') parts.shift();
    return parts.join('0');
}


